import React, { Component } from 'react';
import { Navbar, NavbarBrand, Nav, NavbarToggler, Collapse, NavItem, Jumbotron,
    Button, Modal, ModalHeader, ModalBody,
    Form, FormGroup, Input, Label } from 'reactstrap';
import { HashLink as Link } from 'react-router-hash-link';

class Header extends Component{
    constructor(props) {
        super(props);       
      }


    render(){
        return(
            <div>
            <nav class="navbar navbar-expand-lg navbar-dark fixed-top" id="mainNav">
                <div class="container">
                    <Link class="navbar-brand js-scroll-trigger" to="#page-top">Green Technology Project</Link>
                    <button class="navbar-toggler navbar-toggler-right" type="button" data-toggle="collapse" data-target="#navbarResponsive" aria-controls="navbarResponsive" aria-expanded="false" aria-label="Toggle navigation">
                        Menu
                        <i class="fas fa-bars ml-1"></i>
                    </button>
                    <div class="collapse navbar-collapse" id="navbarResponsive">
                        <ul class="navbar-nav text-uppercase ml-auto">
                            <li class="nav-item"><Link class="nav-link js-scroll-trigger" to="smartcity-project#themen">Themen</Link></li>
                            <li class="nav-item"><Link class="nav-link js-scroll-trigger" to="smartcity-project#mobility">Mobility</Link></li>
                            <li class="nav-item"><Link class="nav-link js-scroll-trigger" to="smartcity-project#smartcity">Smartcity</Link></li>
                            <li class="nav-item"><Link class="nav-link js-scroll-trigger" to="smartcity-project#stats">Stats</Link></li>
                        </ul>
                    </div>
                </div>
            </nav>
            <header class="masthead" id="page-top">
                <div class="container" >
                    <div class="masthead-subheading">Wie neue Technolgien den Klimawandel stoppen</div>
                    <div class="masthead-heading text-uppercase">Die Stadt der Zukunft</div>
                    <a class="btn btn-primary btn-xl text-uppercase js-scroll-trigger " href="#themen">Tell Me More</a>
                </div>
            </header>
        </div>
        );
    }
}

export default Header;